.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(0, 0, 0, .5);
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  z-index: 30;
  text-align: center;
  background-color: #131313;
  border-radius: 3px;
  padding: 30px;
  transition: all .3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

.data-body-stop {
  overflow: auto;
}

.data-body-stop.active {
  touch-action: none;
  height: 100vh;
  overflow: hidden;
}

.modal.active {
  opacity: 1;
  visibility: visible;
}

.overlay.active {
  opacity: 1;
  visibility: visible;
  height: 100vh;
  width: 100vw;
}

.modal__cross {
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

@media screen and (min-width: 768px) {
  .modal__cross {
    width: 24px;
    height: 24px;
  }
}

.modal__cross > svg {
